@import url('../../../css/inter-fonts.css');
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

.tb_sp_modal{
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    line-height: 1.42857;
    color: #262626;
}

.tb_sp_modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 9999999;
    opacity: 0;
    background: rgba(0, 0, 0, 0.7);
    -webkit-transition: opacity 0.3s ease-out 0s;
    -moz-transition: opacity 0.3s ease-out 0s;
    -ms-transition: opacity 0.3s ease-out 0s;
    -o-transition: opacity 0.3s ease-out 0s;
    transition: opacity 0.3s ease-out 0s;
}
.tb_sp_modal.in{
    opacity: 1;
}

a:hover, a:active, a:focus {
    text-decoration: none;
}

button:focus {
    outline: none;
    outline: -webkit-focus-ring-color auto 0px;
}
.tb_sp_modal.show {
  display: block;
  opacity:1;
  visibility: visible;
  transition: opacity 300ms ease-in-out;
}

.tb_sp_fade {
    -webkit-transition: opacity 0.15s linear;
    transition: opacity 0.15s linear;
}

.tb_sp_modal_dialog.show {
    -webkit-transform: none;
    transform: none;
}

.tb_sp_modal_dialog {
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    min-height: calc(100% - 3.5rem);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    width: 100%;
    padding: 20px;
    pointer-events: none;
    max-width: 1260px;
    z-index: 1;
    margin: 0rem auto;
    opacity: 0;
    -moz-transition-property: -moz-transform,-webkit-transform,-moz-transform,-ms-transform,opacity;
    -o-transition-property: -o-transform,-webkit-transform,-moz-transform,-ms-transform,opacity;
    -webkit-transition-property: -webkit-transform,-webkit-transform,-moz-transform,-ms-transform,opacity;
    transition-property: transform,-webkit-transform,-moz-transform,-ms-transform,opacity;
    -moz-transition-duration: .2s;
    -o-transition-duration: .2s;
    -webkit-transition-duration: .2s;
    transition-duration: .2s;
    -moz-transition-timing-function: ease-in;
    -o-transition-timing-function: ease-in;
    -webkit-transition-timing-function: ease-in;
    transition-timing-function: ease-in;
    transition: all ease .3s;
    -moz-transition: all ease .3s;
    -webkit-transition: all ease .3s;
    -o-transition: all ease .3s;
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    transform: scale(0.8);
}

.tb_sp_modal.in .tb_sp_modal_dialog,
.tb_sp_modal.show .tb_sp_modal_dialog{
    opacity: 1;
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
}

.tb_sp_modal_body {
    margin: 0 auto;
}

.tb_sp_post_wrap_in{
    height: 100%;
    padding:0 20px;
}

.tb_sp_conetent {
    transition: all .25s;
    position: relative;
    max-height: 100%;
    max-width: 100%;
    width: auto;
}
.tb_sp_conetent {
    pointer-events: auto;
    background-clip: padding-box;
    outline: 0;
    margin: 0 auto;
}

.tb_sp_media_holder {
    width: calc(100vh - 100px);
    height: 100%;
    max-width: 500px;
    min-width: 320px;
}

.tb_sp_media_wrapper_in {
    width: 100%;
}

.tb_sp_img_holder_wrap {
    position: relative;
    text-align: center;
    padding-bottom: 160%;
    width: 100%;
    border-radius: 8px;
    overflow: hidden;
}

.tb_sp_img_holder {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1;
    height: 100%;
    width: 100%;
    /* background-color: #f7f7f7; */
}

.tb_sp_img_holder:after{
    content: '';
    position: absolute;
    top: 0;
    width: 100%;
    height: 120px;
    left: 0;
    right: 0;
    z-index: 1;
    background: -moz-linear-gradient(top,  rgba(0,0,0,0.25) 0%, rgba(0,0,0,0) 80%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  rgba(0,0,0,0.25) 0%,rgba(0,0,0,0) 80%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  rgba(0,0,0,0.25) 0%,rgba(0,0,0,0) 80%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#bf000000', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
}
.tb_sp_content_wrap{
  width:100%;
}

.tb_sp_details_left {
    position: relative;
    vertical-align: middle;  
}

.tb_sp_media_wrapper_in .slick-slider, .tb_sp_media_wrapper_in .slick-list {
    height: 100%;
}



.tb_sp_img {
    width: 100%;
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_sp_close_wrap {
    position: absolute;
    right: -70px;
    top: 0;
    z-index: 9;
}

.tb_sp_close_btn,
.tb_sp_nav{
    border:none;
    border-radius: 4px;
    display: inline-block;
    background-color: transparent;
    line-height: 0;
    transition: all .3s;
    font-size: 7px;
    position: relative;
    cursor: pointer;
}
.tb_sp_close_btn {
    padding: 10px;
}

.tb_sp_nav{
    padding: 6px 4px;
}

.tb_sp_nav svg{
  height: 35px;
  width: 35px;
}

.tb_sp_close_btn svg{
  height: 18px;
  width: 18px;
}

.tb_sp_close_btn svg g [fill],
.tb_sp_nav svg g [fill]{
  fill: #fff;
}
.tb_sp_nav {
    position: absolute;
    top: 50%;
    z-index: 1001;
    margin-top: -20px;
    left: 0;
    right: 0;
    z-index: 2;
}
.tb_sp_nav_img{
    display: none;
}

.tb_sp_nav {
    cursor: pointer;
    color: #fff;
}

.tb_sp_nav:hover,
.tb_sp_close_btn:hover{
    background-color: rgba(97,97,97,0.3);
}

.tb_sp_prev_btn{
  right: auto;
  left: -70px;
}

.tb_sp_nxt_btn{
    left: auto;
    right: -70px;
}

.tb_sp_prev_arrow_in_00, .tb_sp_next_arrow_in_00 {
    position: absolute;
    height: 30%;
    width: 50%;
    z-index: 1;
    cursor: pointer;
    top: 35%;
    bottom: 0;
}

.tb_sp_prev_arrow_in_00{
    left: 0;
    right: auto;
}

.tb_sp_next_arrow_in_00{
    right: 0;
    left: auto;
}

.tb_sp_multiple_slide_wrapper > .tb_sp_multiple_slide_pro{
    display: none !important;
}

.tb_sp_multiple_slide_pro{
    padding: 0;
    margin: 0;
    list-style: none;
    position: absolute;
    top: 0;
    width: 100%;
    height:auto;
    left: 0;
    right: 0;
    z-index: 9;
    display: flex !important;
    align-items: center;
    padding: 3px 5px;
}
.tb_sp_multiple_slide_pro li {
    display: flex;
    height: auto;
    width: 100%;
    padding: 2px;
}

.tb_sp_slide_dot_single.tb_sp_multiple_slide_pro li{
    display: none;
}
.tb_sp_slide_dot_single.tb_sp_multiple_slide_pro li.slick-active {
    display: flex;
}

.tb_sp_multiple_slide_bar {
    width: 100%;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.50);
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.tb_sp_slide_progress{
    position: absolute;
    height: 100%;
    width: 0%;
    border-radius: 30px;
    background-color: #fff;
}

.tb_sp_slide_progress.tb_sp_seen_slide{
    width: 100%;
}

.tb_sp_multiple_slide_pro .slick-active .tb_sp_slide_progress{
    -webkit-animation: tb_progress_animation linear;
	animation: tb_progress_animation linear;
}

@-webkit-keyframes tb_progress_animation {
    0% {
        width: 0%;
    }
    100% {
        width: 100%;
    }
  }
  @keyframes tb_progress_animation {
    0% {
        width: 0%;
    }
    100% {
        width: 100%;
    }
  }
  
  .tb_sp_content {
    position: absolute;
    top: 10px;
    width: 100%;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
}

.tb_sp_user_img {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    margin-right: 15px;
}

.tb_sp_user {
    display: flex;
    align-items: center;
    justify-content: start;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
}

.tb_sp_username {
    font-size: 16px;
    color: #fff;
    font-weight: 600;
    display: inline-block;
    max-width: 230px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tb_sp_share_btn {
    padding: 8px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all .2s ease-in;
}

.tb_sp_share_btn:hover{
    background-color: rgba(255, 255, 255, 0.1);
}

.tb_sp_share_btn svg {
    height: 20px;
    width: 20px;
}

.tb_sp_share_wrap {
    position: relative;
}

.tb_sp_share_list {
    position: absolute;
    top: 110%;
    right: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: 100%;
    background-color: #fff;
    border-radius: 2px;
    padding: 5px 0;
    opacity: 0;
    visibility: hidden;
    transform: translateY(25px);
    transition: all .2s linear;
}
.tb_sp_share_list.tb_sp_toggle {
    transform: translateY(0px);
    opacity: 1;
    visibility: visible;
}
.tb_sp_share_items {
    padding: 6px 10px;
    display: flex;
    align-items: center;
    justify-content: start;
    cursor: pointer;
    text-decoration: none;
    transition: all .2s ease-in;
}

.tb_sp_share_items:hover{
    background-color: #f7f7f7;
}

.tb_sp_share_txt {
    margin-left: 10px;
    font-size: 13px;
    font-weight: 500;
}
.tb_sp_share_items svg {
    width: 16px;
    height: 16px;
}

@media screen and (max-width: 767px) {
.tb_sp_nxt_btn {
    right: 0;
}
.tb_sp_prev_btn {
    left: 0;
}

.tb_sp_close_wrap {
    right: 15px;
    top: 28px;
}
.tb_sp_share_post {
    margin-right: 40px;
}
.tb_sp_nav svg{
    height: 30px;
    width: 30px;
}
.tb_sp_close_btn svg {
    height: 15px;
    width: 15px;
}
.tb_sp_close_btn {
    border-radius: 50%;
}
}

@media screen and (max-width: 580px) {
.tb_sp_modal_dialog {
    padding: 0;
    margin: 0;
}
.tb_sp_media_holder {
    max-width: 580px;
    width: 100vw;
}
.tb_sp_img_holder_wrap {
    padding-bottom: 100vh;
    border-radius: 0;
}
.tb_sp_media_wrapper_in{
    height: 100vh;
}
}

.web-share-fade {
    position: absolute!important;
    z-index:9999999!important;
}