.tb_multiple_slider01 {
  width: 100%;
  height: 100%;
  transform: scale(1.003);
}

.tb_muliple_images{
  display: block;
  width: 100%;
  height: 100%;
}
.tb_slider_img_wrap00{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  height: calc(25vh + 25vw);
  position: relative;
}
.tb_slider_img_wrapp001{
  position: relative;
  z-index: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  max-height: 100%;
  max-width: 100%;
  width: 100%;
  height: 100%;
}

.tb-detail-image{
    max-width: 100%;
    display: -webkit-flex;
    display: flex;
    width: auto;
    height: auto;
    max-height: 100%;
    margin: 0 auto;
}

.tb_slider_dots {
  position: absolute;
  bottom: 15px;
  text-align: center;
  list-style: none;
  padding: 0;
  width: 100%;
  margin: 0;
}
.tb_slider_dots li{
  display: inline-block;
  padding: 5px;
}

.tb_slider_dots li button {
  font-size: 0;
  background-color: transparent;
  border: none;
  height: 14px;
  width: 14px;
  border-radius: 50%;
  display: block;
  padding: 0;
  border: 2px solid #fff;
  cursor: pointer;
}

.tb_slider_dots li.slick-active button {
  background-color: #fff;
}

.tb_next_arrow_00, .tb_prev_arrow_00  {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 50%;
  margin-top: -16px;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 2px 1px;
}

.tb_next_arrow_00 svg, .tb_prev_arrow_00 svg{
  border-radius: 50%;
  -webkit-box-shadow: 0 1px 2px rgb(0 0 0 / 8%), 0 2px 4px rgb(0 0 0 / 6%), 0 5px 14px rgb(0 0 0 / 4%);
  box-shadow: 0 1px 2px rgb(0 0 0 / 8%), 0 2px 4px rgb(0 0 0 / 6%), 0 5px 14px rgb(0 0 0 / 4%);
  border-radius: 50%;
  position: relative;
}

.tb_next_arrow_00{
  right: 15px;
  left: auto;
}
.tb_prev_arrow_00{
  left: 15px;
  right: auto;
}
.slick-slide > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 1024px) {
  .tb_slider_img_wrap00{
    height: calc(23vh + 23vw);
  }
}
@media screen and (max-width: 991px) {
  .tb_slider_img_wrap00 {
    height: 100%;
}

} 

@media screen and (max-width: 767px) {
.tb_next_arrow_00, .tb_prev_arrow_00  {
  display: none !important;
}
}