.tabbable .nav-tabs>li>a:hover:before{ left:0px; top:0px; width:100%; height:100%;  opacity:1; visibility:visible;}
.tabbable .nav-tabs>li>a:hover, .tabbable .nav-tabs>li>a:focus{ border:0px solid #ff4051 !Important; color:#ff4051; background:transparent; border:0px !important;} 
