/***New toggle button start***/
.onoffswitch {
    position: relative;
    width: 48px;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select: none;
    margin: 0 auto;
    margin-top:5px;
}
.onoffswitch.toLeft {margin-left: 0px;}
.onoffswitch-checkbox {  display: none;}
.onoffswitch-label {
    display: block; overflow: hidden; cursor: pointer;
    border-radius: 20px;
    -moz-border-radius: 20px;
    -webkit-border-radius: 20px;
}
.onoffswitch-inner {
    display: block; width: 200%; margin-left: -100%;
    transition: margin 0.3s ease-in 0s;
    -webkit-transition: margin 0.3s ease-in 0s;
}
.onoffswitch-inner:before, .onoffswitch-inner:after {
    display: block;
    float: left;
    width: 50%;
    height: 16px;
    padding: 0;
    line-height: 16px;
    font-size: 10px;
    color: white;
    font-family: Trebuchet, Arial, sans-serif;
    font-weight: bold;
    box-sizing: border-box;
}
.onoffswitch-inner:before {
    content: "ON";    
    padding-left: 8px;
    background-color: #e6e6e6;
    color: #ff4051;
    padding-right: 24px;
}
.onoffswitch-inner:after {
    content: "OFF";
    padding-right: 8px;
    background-color: #DBDBDB; color: #646464;
    text-align: right;
}
.onoffswitch-switch {
    display: block;
    width: 10px;
    height: 10px;
    margin: 3px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 30px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    transition: all 0.3s ease-in 0s;
    -webkit-transition: all 0.3s ease-in 0s;
    box-shadow: 0px 3px 10px #b1b1b1;
    -moz-box-shadow: 0px 3px 10px #b1b1b1;
    -webkit-box-shadow: 0px 3px 10px #b1b1b1;
    background-color: #fff;
}
.onoffswitch-switch.off{ background:#b9b9b9;}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner { margin-left: 0;}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {right: 0px; background:#ff4051 !important;}

/***New toggle button End***/

