@media screen and (max-width: 580px) {
.tb_sp_modal_dialog {
    padding: 0;
    margin: 0;
}
.tb_sp_media_holder {
    max-width: 580px;
    width: 100vw;
}
.tb_sp_img_holder_wrap {
    padding-bottom: 100vh;
