}

.tb_powred_logo {
    display: flex;
    align-items: center;
    justify-content: end;
}
.tb_powred_logo svg path{
    fill: #fff;
}

.tb_logo_icon__ {
    width: 20px;
    height: 20px;
}
.tb_logo_text__{
    margin-left: 2px;
    height: 26px;
    width: 76px;
}
.tb_pro_name {
    color: #fff;
    line-height: 20px;
    margin-left: 5px;
    font-family: 'Comfortaa', cursive;
    font-size: 12px;
}

.tb_video_wrap video {
    height: auto;
    max-height: 100%;
