    color: #007bff;
    font-weight: 600;
    text-decoration: none;
}

.tb_modal_iframe_body {
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0 0.25rem 0.5rem rgb(0 0 0 / 10%);
    width: 575px;
    position: relative;
    min-height: 700px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tiktok-embed{
    margin: 0 !important;
}

.tb_modal_iframe_wrapper {
    height: 100%;
    width: 100%;
    padding: 15px;
}

@media screen and (max-width: 1024px) {
    .tb_media_holder {
        width: calc(23vh + 23vw);
    }
    .tb_slider_img_wrap00{
        height: calc(23vh + 23vw);
    }
    .tb_post_wrapper {
        height: calc(23vh + 23vh - 120px);
    }
}

@media screen and (max-width: 991px) {

.tb_navigation .tb_prev_btn{
  left: -50px;
}
.tb_post_detail_wrap {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}

.tb_close_wrap {
    right: -50px;
}
.tb_navigation .tb_nxt_btn{
    right: -50px;
}
.tb_modal_body {
    flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    justify-content: start;
}

.tb_details_left,
.tb_details_right {
    width: 100%;
    height: auto;
    position: relative;
    flex-direction: column;
}

.tb_media_holder {
    position: relative;
    text-align: center;
    width: 100%;
    height: 100%;
     min-width: auto;
    display: block;
    padding-bottom: 0;
}

.tb_media_wrapper_in {
    position: relative;
}

.tb_post_wrapper {
    height: auto;
}
.tb_modal_body{
    width: 100%;
}

.tb_post_wrap_in {
    padding: 0 0px;
    width: 100%;
}

.tb_content_wrap{
    display: flex;
    flex-direction: column;
    justify-content: end;
    flex-grow: 1;
    height: 100%;
}

}

@media screen and (max-width: 767px) {
