.poweredbytaggbox__ .tb_post_footer_social_ {
    justify-content: center;
}

.tb_post_footer_social_ {
    justify-content: end;
}

.tb_details_right{
    flex-grow: 1;
    align-items: start;
}

.tb_poweredby_footer {
    position: relative;
    bottom: 0;
}

.tb_post_link__{
    display: none;
}
.modal-only-text .tb_post_wrapper {
