}

.tb_modal_head {
    background-color: #fff;
}
.tb_modal_dialog{
  padding: 0;
  margin: 0 auto;
}

.tb_pro_name {
    color: rgba(0,0,0,.2);
}
.tb_powred_logo svg path {
    fill: rgba(0,0,0,0.2);
}
.tb_navigation .tb_prev_btn{
  left: 5px;
}
.modal-only-text .chtrLimits {
    font-size: 18px;
}
.tb_navigation .tb_nxt_btn{
    right: 5px;
}

.tb_close_btn svg, .tb_nav svg {
    height: 30px;
    width: 30px;
}

.tb_close_btn, .tb_nav {
    padding: 3px 4px;
}

.tb_navigation .tb_nav {
    background-color: rgba(97,97,97,0.3);
}

.tb_navigation.tb_nav_img{
    display: block;
}
.tb_navigation.tb_nav_content{
    display: none;
}

.modal-only-text .tb_navigation.tb_nav_img{
    display: none;
}
.modal-only-text .tb_navigation.tb_nav_content{
