}

.tb_conetent {
    min-height: 100vh;
}

.modal-only-text .tb_post_content {
    min-height: calc(100vh - 177px);
}
.tb_modal_header{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 10px 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.tb_modal_header .tb_post_address a{
    color:rgba(0,0,0,.3);
}

.tb_close_wrap {
    position: relative;
    right: 0;
    top: 0;
    display: block;
    text-align: right;
}

.tb_close_btn:hover {
    background-color: rgb(255 255 255 / 30%);
}

.tb_close_btn svg g [fill] {
    fill: rgba(0,0,0,.8);
}
.poweredbytaggbox__ .tb_poweredby_footer {
    justify-content: space-between;
}
.tb_modal_iframe_body {
    width: 100%;
    position: relative;
    max-width: 100%;
    min-height: calc(100vh - 60px);
    height: auto;
}
.tb_modal_iframe_wrapper {
    padding: 0px;
}
}

@media screen and (max-width: 480px) {
.tb_logo_text__ {
    display: none;
}
}

@font-face {
    font-family: 'Post_Submit_Fonts';
    src:  url('https://cloud.taggbox.com/widget/widget-embed/font/post_submit.eot?pdk9p');
    src:  url('https://cloud.taggbox.com/widget/widget-embed/font/post_submit.eot?pdk9p#iefix') format('embedded-opentype'),
      url('https://cloud.taggbox.com/widget/widget-embed/font/post_submit.ttf?pdk9p') format('truetype'),
      url('https://cloud.taggbox.com/widget/widget-embed/font/post_submit.woff?pdk9p') format('woff'),
      url('https://cloud.taggbox.com/widget/widget-embed/font/post_submit.svg?pdk9p#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}
  
.tb-sp-font, .fa-onsite-upload {
    font-family: 'Post_Submit_Fonts' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    display: inline;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.tb-sp-camera-icon:before {
    content: "\e901";
}
.tb-sp-add-image-icon:before {
    content: "\e902";
}
.tb-sp-image-icon:before {
    content: "\e903";
}
.icon-fa-onsite-upload:before, .fa-onsite-upload:before {
    content: "\e900";
}

.text_links_{
    color: #0000EE!important;
    text-decoration: none;
    background-color: transparent;
}