button{
    cursor: pointer;
}
.onsite_submit_post_main{
    background-color: #e8e8e8;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1055;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    outline: 0;
    background-size: cover;
    background-repeat: no-repeat;
}
.onsite_submit_post_inner{
    max-width: 550px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    min-height: calc(100% - 1rem);
}
.submit_post_box{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 5px;
    outline: 0;
    overflow: hidden;
}
.submit_post_box .box_header{
    padding: 15px;
    text-align: center;
    padding-bottom: 0px;
}
.popup_logo{
    margin-top: 20px;
}
.popup_logo_img{
    max-width: 200px;
    object-fit: contain;
}
.submit_post_close_btn{
    position: absolute;
    right: 15px;
}
.submit_post_close_btn svg{
    cursor: pointer;
}
.box_title{
    text-align: center;
    padding-bottom: 15px;
}
.box_title .title{
   margin-bottom: 10px;
    font-size: 22px;
    font-weight: 700;
    margin-top: 15px;
}
.box_title .sub_heading{
    margin: 0px;
        font-size: 16px;
}
.box_body{
    background-color: #f8fafc;
    border-top: 1px solid #E5E5E5;
}
.box_body .tabs_nav{
    list-style: none;
    padding: 0px;
    margin: 0px;
    display: flex;
    justify-content: center;
    background-color: #fff;
}
.box_body .tabs_nav li{
    width: 50%;
    text-align: center;
    padding: 25px 15px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.box_body .tabs_nav li.active{
    background-color: #F0F2F3;
    color: #FF4050;
}
.submit_post_tabs{
    position: relative;
}
.submit_post_tabs .saprator{
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    background-color: #fff;
    padding: 10px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    border: 1px solid #F0F2F3;
}
.post_tabs_content{
    padding: 30px;
}
.post_tabs_content .image_upload_box{
    background-color: #fff;
    padding: 15px;
}
.add_caption_tab .image_upload_box{
    background-color: #fff;
    padding: 10px;
    border: 1px solid #E9ECED;
    border-radius: 3px;
    margin-bottom: 15px;
}
.image_upload_box_content{
    background-color: #F0F2F3;
    text-align: center;
    height: 250px;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 5px;
    overflow: hidden;
}
.image_upload_box_content img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.image_upload_box_content .type_icon{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    transform: translate(-50%, -50%);
}
.image_upload_box_content .type_icon svg{
    width: 100%;
    height: 100%;
}
.image_upload_box_content .trash_icon{
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #e9e9e9;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    padding: 5px;
      cursor: pointer;
}
.image_upload_box_content .trash_icon svg{
    width: 100%;
    height: 100%;
    fill: #545454;
}
.image_upload_box_content h3{
    margin-bottom: 0px;
    margin-top: 10px;
    color: #2B2B2B;
}
.image_upload_box_content p{
    margin-top: 10px;
    margin-bottom: 0px;
    color: #545454;
}
.submit_pic_uploader_status{
    position: absolute;
    bottom: 0px;
    width: 100%;
}
.add-text{
    margin-bottom: 15px;
}
.add-text input{
    width: 100%;
    border: 1px solid #E9ECED !important;
    padding: 20px;
    color: #545454 !important;
    font-size: 16px;
    border-radius: 5px;
    outline: none!important;
    height: auto;
    box-shadow: none !important;
}
.next-step{
    text-align: right;
}
.next-step .btn-primary{
     border: 0px;
    padding: 12px 20px;
    border-radius: 3px;
    font-weight: 600;
    font-size: 16px;
    cursor: pointer;
    background-color: #FF4050 !important;
    color: #fff !important;
    outline: none;
    box-shadow: none !important;
}
.rating-stars{
    margin-bottom: 15px;
}
.rating-stars .react-stars span{
    line-height: 24px;
}
.submit-post-main{
    padding: 30px;
}
.fill-post-box{
    display: flex;
    align-items: start;
    background-color: #fff;
    border: 1px solid #E9ECED;
    padding: 15px;
    border-radius: 10px;
    justify-content: space-between;
    margin-bottom: 15px;
    box-shadow: 0px 0px 3px #e9e9e9;
}
.fill-post-box .post_edit{
    width: 45px;
    margin-left: 5px;
}
.fill-post-box .fill-post-info{
    display: flex;
    width: 100%;
}
.fill-post-box .img-thumb{
    width: 70px;
    height: 70px;
    border-radius: 5px;
    overflow: hidden;
    margin-right: 15px;
}
.fill-post-box .img-thumb img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.fill-post-box .img-thumb ~ .post-details{
    width: 78%;
}
.fill-post-box p{
    margin: 0px;
    color: #545454;
    font-size: 14px;
    font-weight: 500;
     word-break: break-all;
}
.post-details .react-stars span{
    line-height: 32px;
}
.fill-post-box .post_edit button{
    background: transparent;
    border: 0px;
    cursor: pointer;
    padding: 0px;
}
.fill-post-box .post_edit button svg{
    width: 16px;
    height: 16px;
}
.fill-post-box .post_edit button.pencil{
    margin-right: 5px;
}
.following_information .form-group{
    margin-bottom: 15px;
}
.following_information .form-group label{
    display: block;
    color: #2B2B2B;
    font-size: 14px;
    margin-bottom: 5px;
}
.following_information .form-group label span{
    color: #ff4554;
}
.following_information .form-group .form-control{
    width: 100%;
    border: 1px solid #E9ECED;
    padding: 15px;
    color: #545454;
    font-size: 16px;
    border-radius: 5px;
    outline: none;
    height: auto;
}
.following_information p a{
    color: #000;
    font-weight: 600;
}
.following_information .form_footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.following_information .form_footer .btn-addmore{
    background-color: #E5E7E8;
    padding: 15px 20px;
    font-size: 14px;
    border: 0px;
    margin-right: 10px;
    border-radius: 3px;
}
.following_information .form_footer .add_more span{
    font-size: 12px;
}
.following_information .form_footer .add_more span>span{
    color: #FF4050;
}
.following_information .form_footer .btn-primary{
       background-color: #FF4050 !important;
    color: #fff !important;
    padding: 15px 20px;
    font-size: 14px;
    border: 0px;
    margin-right: 0px;
    font-weight: 600;
    border-radius: 3px;
    outline: none;
    box-shadow: none !important;
}
.following_information .form-group .checkbox{
    display: flex;
    cursor: pointer;
    position: relative;
    align-items: center;
    font-weight: normal;
}
.following_information .form-group .checkbox input{
    position: absolute;
    z-index: 1;
    opacity: 0;
}
.following_information .form-group .checkbox span{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    height: 18px;
    width: 18px;
    border-width: 2px!important;
    border: 1px solid #E5E7E8;
    margin-right: 0.75rem;
    background-color: #E5E7E8;
}
.following_information .form-group .checkbox span:after {
    content: '';
    border-color: transparent;
    border-style: solid;
    border-width: 0 2px 2px 0!important;
    -webkit-transform: rotate(45de);
    transform: rotate(45deg);
    margin-top: -2px;
    width: 5px;
    height: 8px;
}
.following_information .form-group .checkbox input:checked~span{
    background-color: transparent;
    border-color: #000;
}
.following_information .form-group .checkbox input:checked~span:after {
    border-color: #2b2b2b;
    background-color: transparent;
}
.success_post .success_post_box{
    padding: 50px;
    text-align: center;
}
.success_post .success_post_box h3{
      margin-top: 0px;
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 700;
}
.success_post .success_post_box p{
  margin-top: 0px;
    margin-bottom: 15px;
    font-size: 16px;
    font-weight: 400;
}
.uppy-DragDrop--isDragDropSupported{
    border: 0px !important;
    background-color: #F0F2F3 !important;
}
.success_post .success_post_box .btn-primary{
       background-color: #FF4050 !important;
    color: #fff !important;
    outline: none;
    box-shadow: none !important;
    padding: 15px 30px;
    font-size: 14px;
    border: 0px;
    margin-top: 20px;
    font-weight: 600;
    border-radius: 3px;   
     margin-top: 0px;
}

.uppy-DragDrop-label{
    font-weight: 700;
    color: #2B2B2B;
}
.uppy-DragDrop-note{
    color: #545454 !important;
    font-size: 14px !important;
}
.image_upload_box_content .uppy-DragDrop-inner::before{
    content: '';
    display: block;
    background: url(data:image/svg+xml;base64,);
    height: 22px;
    width: 25px;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 auto;
    margin-bottom: 10px;
}
.uppy-DragDrop-container:focus{
    box-shadow: none !important;
}
.image_upload_box_content .uppy-DragDrop-inner svg{
    display: none;
}
.error-text{
    font-size: 12px;
    color: #ff4051;
}
.submit-error{
    margin-bottom: 10px;
}
.following_information .terms_condition{
    margin-top: 0px;
    margin-bottom: 20px;
    font-size: 14px;
}

.btn-primary.submit_pic_spinner{
    padding-right: 3rem;
    opacity: 0.6;
    -webkit-box-shadow: none;
    box-shadow: none;
    pointer-events: none;
}

.submit_pic_spinner:after {
    content: "";
    box-sizing: border-box;
    width: 1.4rem;
    display: inline-block;
    height: 1.4rem;
    vertical-align: middle;
    border-radius: 50%;
    border: 2px solid #ffffff;
    border-right: 2px solid transparent;
    animation: submit_pic_animation-spinner 0.5s linear infinite;
    margin-left: 5px;
}

@-webkit-keyframes submit_pic_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
}
  
@keyframes submit_pic_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
}


.btn-primary:not(.submit_pic_disabled):hover {
    background-color: #000;
}
.submit_pic_disabled{
    opacity: 0.6;
    -webkit-box-shadow: none;
    box-shadow: none;
}
.submit_pic_limit_error{
    width: 100%;
    text-align: center;
    justify-content: center;
    margin-bottom: 10px;
}

@media (max-width: 550px) {
    .submit_post_box{
        border: 0px;
        border-radius: 0px;
        height: 100%;
    }
    .box_body{
        height: 100%;
    }
    .popup_logo_img{
        max-width: 150px;
    }
    .box_title .title{
        font-size: 18px;
    }
    .box_title .sub_heading{
        font-size: 14px;
    }
    .box_body .tabs_nav li{
        font-size: 14px;
    }
    .submit_post_tabs .saprator{
        font-size: 12px;
    }
    .post_tabs_content,
    .submit-post-main {
        padding: 15px;
    }
    .image_upload_box_content h3{
        font-size: 14px;
    }
    .image_upload_box_content p,
    .fill-post-box p,
    .terms_condition,
    .following_information .form-group label,
    .following_information .form-group .form-control,
    .add-text input{
        font-size: 12px !important;
    }
    .onsite_submit_post_inner{
        min-height: 100%;
        height: 100%;
    }
    .image_upload_box_content{
        min-height: 218px;
    }
    .fill-post-box{
        padding: 10px;
        border-radius: 5px;
    }
    .fill-post-box .fill-post-info{
        width: 100%;
    }
    .fill-post-box .post_edit{
        width: 50px;
    }
    .fill-post-box .post_edit button{
        padding: 0px;
    }
    .fill-post-box .fill-post-info .post-details{
        width: 70%;
    }
    .following_information .form_footer .btn-addmore,
    .following_information .form_footer .btn-primary{
        padding: 15px;
        font-size: 12px;
    }
    .fill-post-box .img-thumb{
        width: 60px;
        height: 60px;
    }
    .uppy-DragDrop-label{
        font-size: 14px;
    }
    .uppy-DragDrop-note{
        font-size: 12px !important;
    }
    .popup_logo{
        margin-top: 0px;
    }
    .submit_pic_limit_error{
        font-size: 12px;
    }
    .success_post .success_post_box{
        padding: 50px 25px;
        top: 50%;
        position: relative;
    }
    .next-step .btn-primary{
        font-size: 12px;
    }
}